.order {
  margin-bottom: 80px;

  .section__title-lg {
    margin-top: 20px;
    margin-bottom: 30px;
  }

  @include for-desktop {
    margin-bottom: 60px;
  }

  @include mobile {
    .section__title-lg {
      margin-top: 18px;
      margin-bottom: 25px;
    }
  }

  &__back {
    display: flex;
    align-items: center;
    gap: 6px;
    font-weight: 500;
    line-height: 110%;
    color: #B47A4D;

    &::before {
      content: '';
      display: block;
      width: 16px;
      height: 16px;
      background-image: url('../img/order/arrow-left.svg');
    }
    @include mobile {
      font-size: 14px;
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 12px;

    button {
      padding: 8px 20px;
      border-radius: 20px;
      background-color: #fff;
      font-weight: 500;
      color: #727272;
      transition: color .2s linear, background-color .2s linear ;

      &.active {
        pointer-events: none;
        color: #fff;
        background-color: #B47A4D;
      }

      &:hover {
        color: #fff;
        background-color: #B47A4D;
      }
    }

    @include for-desktop {

      button {
        font-size: 14px;
      }
    }
    @include mobile {
      gap: 8px;

      button {
        font-size: 12px;
      }
    }
  }

  &__inner {
    margin-top: 30px;
    display: grid;
    grid-template-columns: 1fr 370px;
    gap: 70px;

    @include for-desktop {
      gap: 20px;
    }

    @include tablet {
      grid-template-columns: 1fr;
    }

    @include small-tablet {
      gap: 28px;
    }
  }

  .checkout {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include for-desktop {
      gap: 12px;
    }

    @include mobile {
      gap: 28px;
    }
  }

  &__title {
    margin-bottom: 20px;
    font-size: 24px;
    font-weight: 500;

    @include mobile {
      font-size: 20px;
    }
  }

  &-delivery,
  &-products,
  .customer_details,
  .payment {
    padding: 24px;
    border-radius: 20px;
    background-color: #fff;

    @include for-desktop {
      padding: 20px;
    }
  }

  &-delivery {

    .delivery__methods {
      grid-template-columns: repeat(3, 1fr);
      gap: 14px;

      @include small-tablet {
        grid-template-columns: 1fr 1fr;
      }

      @include mobile {
        grid-template-columns: 1fr;
        gap: 12px;
      }

      label {
        padding: 14px;
        border-radius: 16px;
        gap: 57px;
        background-color: #F6F6F4;
        border-color: #F6F6F4;
        transition: border-color 0.2s ease-out, background-color 0.2s ease-out;

        &:hover {
          background-color: #EFEFED;

          .delivery__text {
            color: #A2A2A2;
          }
        }

        @include for-desktop {
          gap: 32px;
        }

        @include mobile {
          gap: 16px;
        }
      }

      .delivery__title {
        margin-bottom: 8px;
        font-size: 16px;

        @include for-desktop {
          font-size: 14px;
        }

        @include mobile {
          font-size: 16px;
        }
      }

      .delivery__text {
        font-size: 14px;

        @include for-desktop {
          font-size: 12px;
        }
      }

      .delivery__amount {
        font-size: 16px;
        font-weight: 500;
        align-self: flex-start;

        @include mobile {
          font-size: 14px;
        }
      }
    }
  }

  .woocommerce-billing-fields__field-wrapper {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px 30px;

    @include for-desktop {
      gap: 20px;
    }

    @include mobile {
      gap: 14px 12px;
      grid-template-columns: repeat(3, 1fr);
    }

    p {
      display: flex;
      flex-direction: column;
      gap: 10px;

      &.notes {
        grid-column: span 2;
      }

      @include mobile {
        gap: 8px;
        grid-column: span 3;

        &.notes {
          grid-column: span 3;
        }

        &.address-field {
          grid-column: span 1;
        }
      }
    }

    label {
      font-family: inherit;
      font-size: 16px;
      color: #444;

      @include for-desktop {
        font-size: 14px;
      }
    }

    input, textarea {
      padding: 14px 16px;
      width: 100%;
      font-family: inherit;
      font-size: 16px;
      color: #222;
      font-weight: 400;
      border-radius: 8px;
      outline: none;
      border: 1px solid #DCDCDC;

      transition: border-color .2s linear;

      &:focus {
        border-color: #B47A4D;
      }
      &:hover {
        border-color: #A2A2A2;

        @include small-tablet {
          border-color: #B47A4D;
        }
      }
      &::placeholder {
        color: #727272;
      }
    }

    textarea {
      height: 102px;
      resize: none;
    }
  }

  .payment {
    margin-top: 0;
    margin-bottom: 0;

    ul {
      grid-template-columns: repeat(3, 1fr);
      gap: 12px;

      @include small-tablet {
        grid-template-columns: 1fr 1fr;
      }
      @include mobile {
        grid-template-columns: 1fr;
      }
    }

    label {
      padding: 16px;
      display: block;
      background-color: #F6F6F4;
      border-color: #F6F6F4;
      border-radius: 16px;
      height: 100%;

      @include for-desktop {
        padding: 14px;
      }

      &::before {
        display: none;
      }

      span {
        font-size: 16px;

        @include for-desktop {
          font-size: 14px;
        }
      }
    }

    input[type=radio]:checked + label span {
      color: #444;
    }
  }

  .mini-cart__product {
    grid-template-columns: 1.2fr .8fr;
    gap: 10px;

    @include mobile {
      padding: 0;
      gap: 20px;
      grid-template-columns: 1fr;

      &.sale {
        gap: 6px;
      }
    }
  }

  .mini-product__count {
    justify-self: end;
    gap: 45px;

    @include for-desktop {
      gap: 30px;
    }

    @include mobile {
      padding-left: 107px;
      padding-top: 0;
      border: 0;
      justify-self: unset;

      .mini-product__price {
        display: block;

        span {
          margin-bottom: -2px;
          text-align: right;
        }
      }
    }
  }

  .mini-product__content {
    @include mobile {
      grid-template-columns: 72px 1fr;
    }
  }

  .mini-product__title {
    margin-bottom: 8px;
    padding-right: 0;
  }

  .mini-product__info {
    @include mobile {
      gap: 4px;
      span {
        text-align: right;
      }
    }
  }

  .mini-cart__item + .mini-cart__item {
    margin-top: 20px;
  }

  .cart__collaterals tr.shipping td, .cart__collaterals tr.cart-payment td {
    color: #222;
  }

  &-info {
    margin-top: 20px;
    padding: 24px;
    display: grid;
    grid-template-columns: 48px 1fr;
    gap: 16px;
    border-radius: 20px;
    color: #444;

    @include for-desktop {
      margin-top: 12px;
      padding: 20px;
      font-size: 13px;
      gap: 12px;
    }
  }

}
