.delivery {
  margin-top: 40px;

  @include mobile {
    .section__title {
      margin-bottom: 20px;
    }
  }

  &__methods {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;

    @include for-desktop {
      gap: 20px;
    }

    @include tablet {
      grid-template-columns: repeat(3, 1fr);
    }

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      grid-template-columns: 1fr;
      gap: 12px;
    }

    input[type="radio"] {
      display: none;

      &:checked {
        +label {
          border-color: #B47A4D;

          .delivery__text {
            font-weight: 500;
            color: #F49827;
          }
        }
      }
    }

    label {
      padding: 22px;
      display: flex;
      flex-direction: column;
      height: 100%;
      gap: 50px;
      justify-content: space-between;
      cursor: pointer;
      background-color: #fff;

      transition: border-color .2s ease-out;

      border: 2px solid #fff;
      border-radius: 20px;

      &:hover {
        .delivery__text {
          color: #F49827;
        }
      }

      @include for-desktop {
        padding: 20px;
      }

      @include mobile {
        gap: 20px;
      }
    }
  }

  &__title {
    display: block;
    margin-bottom: 12px;
    font-size: 21px;
    color: #444;
    font-weight: 500;
    line-height: 120%;

    @include for-desktop {
      font-size: 15px;
    }

    @include mobile {
      margin-bottom: 8px;
    }
  }

  &__text {
    line-height: 130%;
    color: #A2A2A2;
    transition: color .2s linear;

    @include for-desktop {
      font-size: 14px;
    }
  }

  &__amount {
    font-size: 24px;
    line-height: 130%;
    color: #B47A4D;
    align-self: flex-end;

    @include for-desktop {
      font-size: 20px;
    }

    @include mobile {
      font-size: 18px;
    }
  }
}
