.accordion__item {
  border-bottom: 1px solid #DCDCDC;
  transition: border-color 0.2s ease-out;

  &:last-child {
    border: 0;
  }

  &:hover {
    border-color: #F49827;
  }
}

.accordion__header {
  position: relative;
  padding: 24px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-family: 'Inter';
  font-size: 18px;
  font-weight: 500;
  line-height: 112%;
  cursor: pointer;
  transition: background-color 0.2s ease-out;

  svg path {
    transition: stroke 0.2s ease-out;
  }
  &:hover svg path {
    stroke: #F49827;
  }

  @include mobile {
    padding: 16px 0;
    font-size: 16px;
  }
}

.accordion__close {
  display: none;
  position: absolute;
  bottom: -48px;
  right: 0;
  width: 24px;
  height: 24px;
  background-color: #fff;
  background-image: url('../img/svg/minus.svg');
  background-repeat: no-repeat;
  background-size: 24px;
}

.accordion__item_show {
  .accordion__close {
    display: block;
  }
}

.accordion__header svg {
  flex-shrink: 0;
  width: 24px;
  height: 24px;
  margin-left: auto;
  transition: transform 0.2s ease-out;
}

.accordion__item_show .accordion__header svg,
.accordion__item_slidedown .accordion__header svg {
  transform: rotate(-90deg);
}

.accordion__item:not(.accordion__item_show) .accordion__body {
  display: none;
}

.accordion__body {
  > div {
    padding: 24px 0;
    border-top: 1px solid #DCDCDC;
  }

  h5 {
    margin-bottom: 10px;
    font-family: 'Inter';
    font-size: 18px;
    font-weight: 500;
    line-height: 112%;
  }

  p {
    line-height: 130%;
    color: #727272;
    + p {
      margin-top: 8px;
    }
  }

  @include mobile {
    h5 {
      font-size: 16px;
    }

    > div {
      padding: 16px 0;
    }
  }
}
