.products {
  margin-top: 60px;

  &--first {
    @include mobile {
      .products__info-item:first-child {
        flex-direction: column;
        align-items: flex-start;
        gap: 4px;
      }
    }
  }

  &__inner {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 30px;

    @include for-desktop {
      grid-template-columns: repeat(3, 1fr);
      gap: 20px;
    }

    @include small-tablet {
      grid-template-columns: repeat(2, 1fr);
    }

    @include mobile {
      gap: 12px;
    }
  }

  &__item {
    padding: 6px 6px 16px 6px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    transition: box-shadow .2s linear;

    &:hover {
      box-shadow: 0px 24px 38px 0px rgba(117, 117, 108, 0.16);

      .products__btns {
        transform: translateY(0);
        opacity: 1;
      }
    }

    &.sale {

      .price__regular {
        color: #F49827;
      }
    }

    .onsale {
      position: absolute;
      top: 10px;
      left: 10px;
      padding: 5px 10px;
      border-radius: 53px;
      background: #F49827;
      font-size: 14px;
      font-weight: 600;
      line-height: 90%;
      color: #fff;
    }

    @include mobile {
      padding-bottom: 12px;
      border-radius: 15px;

      .onsale {
        font-size: 12px;
        left: 6px;
        top: unset;
        bottom: 6px;
      }
    }
  }

  &__img {
    display: block;
    border-radius: 15px;
    overflow: hidden;
    position: relative;

    img {
      width: 100%;
      height: 100%;
      transition: opacity .2s linear;
    }

    &:hover {

      .products__thumbnail {
        opacity: 0;

        +img {
          opacity: 1;
        }
      }
    }
  }

  &__thumbnail {

    +img {
      opacity: 0;
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  &__content {
    margin-top: 12px;
    padding: 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
    position: relative;

    @include mobile {
      padding: 0 4px;
    }
  }

  &__name {
    font-size: 14px;
    font-weight: 500;

    @include mobile {
      font-size: 12px;
    }
  }

  &__price {
    margin-top: 10px;
    margin-bottom: 12px;

    .price__regular {
      font-weight: 500;
    }

    .products__cat {
      font-size: 14px;
      color: #727272;
      vertical-align: top;
    }

    .price__sale {
      display: inline-block;
      margin-left: 8px;
      transform: translateY(-5px);
      font-size: 12px;
      color: #A2A2A2;
      text-decoration: line-through;
    }

    @include mobile {
      margin-top: 6px;
      margin-bottom: 10px;
      position: relative;

      .price__regular {
        font-size: 15px;
      }

      .products__cat {
        font-size: 12px;
        vertical-align: baseline;
      }

      .price__sale {
        margin-left: 0;
        position: absolute;
        bottom: calc(100% - 5px);
        left: 0;
        font-size: 10px;

        transform: translateY(0);
      }
    }
  }

  &__radius {
    width: 41px;
    height: 41px;
    border-radius: 50%;
    position: absolute;
    top: 10px;
    right: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    color: #B47A4D;
    font-weight: 600;
    line-height: 90%;

    @include mobile {
      right: 8px;
    }
  }

  &__info {

    &-item {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &+& {
        margin-top: 7px;
      }
    }

    &-name {
      font-size: 14px;
      color: #727272;

      @include mobile {
        font-size: 13px;
      }
    }

    &-weight {
      font-size: 14px;
      font-weight: 500;
      color: #444;
    }

    &-length {
      display: flex;
      gap: 5px;

      span {
        padding: 4px 6px;
        font-size: 14px;
        font-weight: 500;
        color: #B47A4D;
        border-radius: 6px;
        background: #F6F6F4;

        +span {
          color: #727272;
          border: 1px solid #DCDCDC;
          background: transparent;
        }
      }
    }
  }

  &__btns {
    padding: 16px;
    border-radius: 0 0 22px 22px;
    position: absolute;
    top: 100%;
    left: -6px;
    right: -6px;
    z-index: 12;
    transition: transform .2s linear, opacity .2s linear;
    transform: translateY(-80%);
    opacity: 0;

    .add_to_cart {
      display: block;
      padding: 10px 16px;
      font-weight: 500;
      color: #fff;
      text-align: center;
      border-radius: 8px;
      background: #B47A4D;
      transition: background-color .2s linear, color .2s linear;

      &:hover {
        background-color: #222;
      }

      @include mobile {
        padding: 11px;
      }
    }

    >div {
      padding: 8px 14px;
      font-weight: 500;
      color: #fff;
      border-radius: 8px;
      background: #222;
      display: flex;
      gap: 5px;
      align-items: center;
      justify-content: space-between;

      .add_to_cart,
      .remove_from_cart {
        padding: 2px;
        width: 24px;
        height: 24px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #fff;
        border-radius: 6px;

        svg {
          path {
            stroke: #444;
            transition: stroke .1s linear;
          }
        }

        &:hover {
          background-color: #fff;

          svg {
            path {
              stroke: #B47A4D;
            }
          }
        }
      }
      @include mobile {
        padding-right: 8px;
        padding-left: 8px;

        >div {
          span:last-child {
            display: none;
          }
        }
      }
    }

    @include for-desktop {
      padding: 16px 0 0 0;
      position: static;
      transform: translateY(0);
      opacity: 1;
    }

    @include mobile {
      font-size: 15px;
    }
  }
}
