.product {
  margin-top: 74px;

  @include for-desktop {
    margin-top: 30px;

    .section__title-sm {
      font-size: 28px;
    }
  }

  @include small-tablet {
    margin-top: 24px;

    .section__title-sm {
      font-size: 24px;
    }
  }

  &__inner {
    display: grid;
    grid-template-columns: 1fr 520px;
    gap: 30px;
    align-items: start;

    @include for-desktop {
      grid-template-columns: 1fr 400px;
    }

    @include small-tablet {
      grid-template-columns: 1fr;
    }

    @include mobile {
      gap: 24px;
    }
  }

  &__item {
    padding: 32px;
    border-radius: 20px;

    @include for-desktop {
      padding: 20px;
    }
  }

  &__content {
    margin-top: 20px;

    @include for-desktop {
      margin-top: 16px;
    }

    .products__price {
      margin-top: 0;
      margin-bottom: 24px;

      @include mobile {
        margin-bottom: 20px;
      }
    }

    .price__regular {
      font-size: 20px;

      @include mobile {
        font-size: 16px;
      }
    }

    .products__cat {
      font-size: 16px;
      vertical-align: bottom;

      @include mobile {
        font-size: 14px;
      }
    }

    .products__info-item {
      flex-direction: column;
      align-items: flex-start;
      gap: 12px;
    }

    .products__info-name {
      font-size: 16px;
      color: #A2A2A2;

      @include mobile {
        font-size: 14px;
      }
    }

    .products__info-length {
      gap: 8px;

      span {
        color: #222;
        padding: 6px 9px;

        @include mobile {
          font-size: 12px;
        }

        +span {
          background: #F6F6F4;
          border: 0;
        }

        &.active {
          color: #F6F6F4;
          background-color: #222;
        }
      }
    }
  }

  &-summary {
    padding: 18px;
    border-radius: 11px;
    background: #F6F6F4;

    @include mobile {
      padding: 16px;
    }

    form {
      margin: 20px 0 0;

      display: flex;
      gap: 10px;

      @include mobile {
        margin: 18px 0 0;
      }
    }

    .cart__count {
      background-color: #fff;
    }

    .single_add_to_cart_button {
      flex-grow: 1;
      padding: 14px;
      border-radius: 8px;
      background: #B47A4D;
      color: #fff;
      font-weight: 500;

      @include mobile {
        padding: 12px;
        font-size: 0;
        display: flex;
        justify-content: center;

        &::after {
          content: '';
          display: block;
          width: 24px;
          height: 24px;
          background-image: url('../img/products/shopping-cart.svg');
        }
      }
    }

    &__price {
      display: flex;
      align-items: center;
      justify-content: space-between;

      div {
        font-size: 18px;
        color: #444;
        font-weight: 500;
        line-height: 130%;

        @include mobile {
          font-size: 16px;
        }
      }

      p {
        font-size: 18px;
        font-weight: 600;
        color: #B47A4D;

        @include mobile {
          font-size: 16px;
        }
      }
    }
  }

  &-characteristics {
    margin-top: 30px;

    @include mobile {
      margin-top: 24px;
    }

    &__title {
      font-weight: 500;
      color: #444;
    }

    &__item {
      margin-top: 12px;
      display: flex;
      gap: 4px;

      @include mobile {
        font-size: 14px;
      }

      span {
        display: grid;
        grid-template-columns: max-content 1fr;
        flex-grow: 1;
        gap: 4px;
        color: #A2A2A2;

        &::after {
          content: '';
          display: block;
          margin-bottom: 4px;
          height: 2px;
          width: 100%;
          align-self: flex-end;
          border-bottom: 1px dashed #DCDCDC;
        }
      }

      div {
        color: #444;
      }
    }
  }

  &-diameter {
    margin-top: 24px;
    margin-bottom: 30px;

    &__title {
      color: #A2A2A2;
    }

    &__inner {
      margin-top: 12px;
      display: flex;
      flex-wrap: wrap;
      gap: 8px;
    }

    &__item {
      font-size: 14px;
      font-weight: 500;
      padding: 7px 9px;
      border-radius: 8px;
      background: #F6F6F4;

      &.active {
        background-color: #222;
        color: #F6F6F4;
      }
    }

    @include for-desktop {
      margin-top: 20px;
    }

    @include mobile {
      margin-bottom: 24px;

      &__title {
        font-size: 14px;
      }

      &__item {
        font-size: 12px;
      }
    }
  }
}

.woocommerce-breadcrumb {
  display: flex;
  gap: 8px;
  font-size: 16px;
  line-height: 110%;
  color: #B47A4D;

  a {
    display: flex;
    align-items: center;
    gap: 8px;
    color: #A2A2A2;

    &::after {
      content: '';
      display: block;
      width: 5px;
      height: 5px;
      border-radius: 50%;
      background-color: #DCDCDC;
    }
  }

  @include mobile {
    font-size: 12px;
  }
}


.delivery-and-payment {

  &__inner {
    margin: 80px 0 120px;
    padding: 32px;
    border-radius: 20px;
    background-color: #fff;

    @include for-desktop {
      padding: 20px;
      margin: 56px 0;
    }

    @include mobile {
      margin: 40px 0;
    }
  }

  @include for-desktop {
    .section__title-sm {
      font-size: 24px;
    }
  }

  .delivery {
    margin-top: 0;
  }

  .delivery__methods {
    margin-top: 24px;

    label {
      background-color: #F6F6F4;
      border-color: #F6F6F4;
      transition: background-color .2s linear;

      &:hover {
        background-color: #EFEFED;
      }
    }

    @include for-desktop {
      margin-top: 32px;
    }

    @include mobile {
      margin-top: 20px;
    }
  }

  .payment {
    margin-top: 30px;
    margin-bottom: 0;

    @include for-desktop {
      margin-top: 20px;
    }
    @include mobile {
      margin-top: 40px;
    }

    &__methods {
      margin-top: 24px;

      @include for-desktop {
        margin-top: 32px;
      }

      @include mobile {
        margin-top: 20px;
      }
    }

    ul {
      label {
        background-color: #F6F6F4;
        border-color: #F6F6F4;
        transition: background-color .2s linear;

        &:hover {
          background-color: #EFEFED;
        }

        @include mobile {
          padding: 16px;
          grid-template-columns: 44px 1fr;
          gap: 12px;

          &::before {
            width: 44px;
            height: 44px;
            background-size: contain;
          }

          >div {
            gap: 4px;
          }
        }
      }

      li:first-child label::before {
        background-image: url('../img/delivery-payment/Frame2.svg');
      }

      li:last-child label::before {
        background-image: url('../img/delivery-payment/Frame3.svg');
      }
    }
  }
}

.woocommerce-product-gallery {
  .flex-viewport {
    height: auto !important;
  }

  .woocommerce-product-gallery__wrapper {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include mobile {
      gap: 12px;
    }
  }

  .woocommerce-product-gallery__image {
    position: relative;

    img {
      border-radius: 15px;
    }
  }

  .woocommerce-product-gallery__diameter {
    position: absolute;
    top: 18px;
    right: 18px;
    width: 50px;
    height: 50px;
    background-color: #fff;
    border-radius: 50%;
    vertical-align: baseline;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    line-height: 90%;
    font-weight: 600;
    color: #B47A4D;
  }

  a {
    pointer-events: none;
  }
}
