.nav {

  &__list {
    display: flex;
    gap: 12px;
  }

  &__link {
    padding: 12px 16px;
    border-radius: 8px;
    background: #F6F6F4;
    transition: background-color .2s linear;

    &:hover {
      background-color: #EFEFED;
    }
  }

  @include tablet {
    width: 100%;
    padding-top: 24px;
    position: relative;

    &__list {
      flex-direction: column;
      gap: 10px;
    }

    &__link {
      display: flex;
      align-items: center;
      justify-content: space-between;

      &::after {
        content: '';
        display: block;
        width: 24px;
        height: 24px;
        background-image: url('../img/svg/arrow-right.svg');

      }
    }

    &::before {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 42px;
      height: 4px;
      background-color: #B5B5B5;
      border-radius: 3px;
    }
  }
}
