.payment {
  margin-top: 46px;
  margin-bottom: 120px;

  @include for-desktop {
    margin-top: 56px;
    margin-bottom: 60px;
  }

  @include mobile {
    margin-top: 40px;

    .section__title {
      margin-bottom: 20px;
    }
  }

  input[type="radio"] {
    display: none;

    &:checked {
      +label {
        border-color: #B47A4D;
        span {
          color: #F49827;
        }
      }
    }
  }

  ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;

    @include for-desktop {
      gap: 20px;
    }

    @include small-tablet{
      grid-template-columns: 1fr;
      gap: 12px;
    }

    label {
      padding: 22px;
      display: grid;
      grid-template-columns: 58px 1fr;
      align-items: center;
      gap: 16px;
      border-radius: 20px;
      background-color: #fff;
      cursor: pointer;

      transition: border-color .2s ease-out;
      border: 2px solid #fff;

      span {
        font-size: 21px;
        font-weight: 500;
        line-height: 120%;
        color: #444;
        transition: color .2s linear;
      }

      > div {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include big-desktop {
          flex-direction: column;
          align-items: flex-start;
          gap: 6px;
        }
      }

      .payment__box {
        line-height: 130%;
        color: #A2A2A2;

        @include for-desktop {
          font-size: 14px;
        }
        @include mobile {
          font-size: 12px;
        }
      }

      &:hover {
        span {
          color: #F49827;
        }
      }

      &::before {
        content: '';
        display: block;
        width: 58px;
        height: 58px;
      }

      @include for-desktop {
        padding: 20px;

        span {
          font-size: 16px;
        }
      }

      @include mobile {
        span {
          font-size: 15px;
        }
      }
    }

    li {

      &:first-child label {
        &::before {
          background-image: url('../img/delivery-payment/Frame.svg');
        }
      }

      &:last-child label {
        &::before {
          background-image: url('../img/delivery-payment/Frame1.svg');
        }
      }
    }
  }
}
