.questions {
  margin: 120px 0;

  @include for-desktop {
    margin-bottom: 100px;
  }

  @include small-tablet {
    margin-top: 80px;
    margin-bottom: 80px;
  }

  &__inner {
    display: grid;
    grid-template-columns: 1fr 410px;
    gap: 30px;

    @include for-desktop {
      grid-template-columns: 1fr;
    }

    @include mobile {
      gap: 20px;
    }
  }

  &__item {
    padding: 30px;
    border-radius: 20px;
    align-self: start;

    &:first-child {
      padding: 10px 30px;
    }

    @include for-desktop {
      &-inner {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 28px;
      }
    }

    @include small-tablet {
      &-inner {
        grid-template-columns: 1fr;
        grid-template-columns: auto;
        gap: 22px;
      }
    }

    @include mobile {
      padding: 20px 18px;
      border-radius: 15px;

      &:first-child {
        padding: 4px 18px;
      }
    }
  }

  &__title {
    margin-bottom: 16px;
    font-family: 'Fact Semi Expanded';
    font-size: 28px;
    font-weight: 500;
    line-height: 115%;

    @include for-desktop {
      margin-bottom: 28px;
      font-size: 24px;
    }
    @include mobile {
      margin-bottom: 12px;
      font-size: 20px;
    }
  }

  &__text {
    line-height: 130%;
    color: #727272;
  }

  &__links {
    margin: 20px 0 28px;

    @include for-desktop {
      margin-bottom: 0;
    }

    @include small-tablet {
      margin-top: 16px;
    }
  }

  &__link {
    padding: 11px 16px;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 8px;
    border: 1px solid #222;
    transition: background-color .2s linear, color .2s linear;

    &+& {
      margin-top: 10px;
    }

    path {
      transition: fill .2s linear;
    }

    &:hover {
      background-color: #222;
      color: #fff;

      path {
        fill: #fff;
      }
    }
  }

  form {
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    gap: 10px;

    label {
      font-size: 13px;
      line-height: 130%;
      letter-spacing: -0.13px;
      color: #727272;
      cursor: pointer;

      a {
        color: #B47A4D;
      }

      &:hover {
        a {
          color: #727272;
        }
      }
    }

    @include small-tablet {
      margin-top: 16px;
    }
  }
}
