.header {
  position: absolute;
  top: 12px;
  left: 0;
  width: 100%;
  z-index: 999;
  transform-origin: top center;
  transition: transform .6s cubic-bezier(.25, .46, .45, .94), background-color .5s cubic-bezier(.25, .46, .45, .94);

  &.hide {
    transform: translateY(-90px);
  }

  &.fixed {
    top: 0;
    position: fixed;
    background-color: #fff;
    border-radius: 0px 0px 20px 20px;
    box-shadow: 0px 1px 33px 0px rgba(117, 117, 108, 0.07);

    &.nav-show, &.mini-cart-show {
      background-color: transparent;
    }
  }

  &.nav-show {

    .header__inner {
      transform: translateY(0);
    }

    .header__wrapper {
      &::before {
        display: block;
      }
    }
  }

  &.mini-cart-show {
    .mini-cart {
      opacity: 1;
      visibility: visible;
    }

    .header__wrapper {
      &::before {
        display: block;
      }
    }
  }

  &__wrapper {
    padding: 9px 16px;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    @include tablet {
      position: static;
    }
  }

  &__right,
  &__info,
  &__inner {
    display: flex;
    align-items: center;
  }

  &__inner {
    flex-grow: 1;
    justify-content: flex-end;
    gap: 145px;

    ~div {
      display: flex;
      gap: 12px;
    }

    @include big-desktop {
      gap: 60px;
    }
  }

  &__info {
    gap: 8px;
    justify-content: flex-end;

    @include small-tablet {
      justify-content: space-between;
    }
  }

  &__schedule {
    font-family: 'Inter';
    font-size: 13px;
    color: #727272;
  }

  &__email {
    display: inline-block;
    margin-top: 3px;
    font-weight: 500;
    color: #F49827;
    transition: color .2s linear;

    &:hover {
      color: rgba(180, 122, 77, 1);
    }
  }

  &__basket {
    margin-left: 24px;
    padding: 10px 16px;
    border-radius: 8px;
    border: 1px solid #222;
    display: flex;
    align-items: center;
    gap: 8px;
    transition: color .2s linear, background-color .2s linear;

    &:hover,
    &:focus {
      color: #fff;
      background-color: #222;
    }

    span {
      &:first-child {
        font-weight: 500;
      }

      &:last-child {
        width: 16px;
        height: 16px;
        font-size: 13px;
        font-weight: 600;
        color: #fff;
        border-radius: 50%;
        background: #F49827;
      }
    }
  }

  @include big-desktop {
    .logo {
      width: 135px;
    }
  }

  @include for-desktop {
    &__right {
      display: none;
    }

    &__inner {
      flex-grow: unset;
    }
  }

  @include tablet {
    &__right {
      display: flex;
    }

    &__wrapper {
      border-radius: 15px;

      &::before {
        content: '';
        display: none;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.16);
      }
    }

    &__inner {
      padding: 12px 20px 25px 20px;
      flex-direction: column;
      gap: 32px;
      position: absolute;
      top: calc(100vh - 300px);
      left: 0;
      z-index: 50;
      width: 100%;
      background-color: #fff;
      transition: transform .6s ease-out;
      transform: translateY(-100vh);
      border-radius: 15px 15px 0px 0px;
    }

    &__basket {
      margin-left: 0;
    }
  }

  @include mobile {

    &__wrapper {
      padding: 8px;
      gap: 10px;
    }

    .logo {
      width: 100px;
    }

    &__basket {
      padding-left: 14px;
      padding-right: 14px;
      font-size: 14px;
    }
  }
}
