.burger {
  padding: 0;
  // width: 40px;
  // height: 40px;
  display: none;
  justify-content: center;
  align-items: center;
  border: 0;
  cursor: pointer;
  position: relative;
  border-radius: 8px;
  // border: 1px solid #222;
  background-color: #fff;

  @include tablet {
    display: flex;
  }
}

.burger:focus {
  outline: none;
  background-color: #222;
  path {
    fill: #fff;
  }
}

