.section {

  &__title {
    margin-bottom: 40px;
    font-family: 'Fact Semi Expanded';
    font-weight: 500;
    font-size: 40px;
    line-height: 115%;

    @include for-desktop {
      margin-bottom: 30px;
      font-size: 36px;
    }
    @include mobile {
      margin-bottom: 24px;
      font-size: 24px;
    }

    &-lg {
      font-family: 'Fact Semi Expanded';
      font-weight: 500;
      margin-bottom: 0;
      font-size: 56px;
      line-height: 112%;

      @include for-desktop {
        font-size: 42px;
      }
      @include tablet {
        font-size: 30px;
      }
    }

    &-sm {
      margin-bottom: 0;
      font-family: 'Fact Semi Expanded';
      font-weight: 500;
      font-size: 32px;
      line-height: 102%;

      @include for-desktop {
        font-size: 24px;
      }
      @include small-tablet {
        font-size: 20px;
      }
    }
  }
}
