.form {

  &__control {
    padding: 14px 16px;
    width: 100%;
    outline: none;
    border-radius: 8px;
    border: 1px solid #DCDCDC;
    font-size: 16px;
    color: #727272;

    &::placeholder {
      color: #A2A2A2;
    }

    &:focus {
      border-color: #B47A4D;
    }
  }

  &__submit {
    padding: 15px 16px;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 500;
    color: #fff;
    cursor: pointer;
    text-align: left;
    background-color: #B47A4D;
    background-image: url('../img/svg/arrow-up-right-1.svg');
    background-size: 24px;
    background-repeat: no-repeat;
    background-position: calc(100% - 16px) 13px;
    transition: background-color .2s linear, color .2s linear;

    &::after {
      content: '';
      display: block;
      width: 24px;
      height: 24px;
    }

    &:hover {
      background-color: #222;
      color: #fff;
    }
  }
}
