.contacts {
  margin: 0 0 80px;

  @include mobile {
    margin: 0 0 60px;
  }

  &__wrapper {
    padding: 40px;
    border-radius: 20px;
    background-color: #222;

    @include mobile {
      padding: 20px 18px;
    }
  }

  &__inner {
    padding-top: 24px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;

    @include mobile {
      padding-top: 18px;
      grid-template-columns: 1fr;
      gap: 24px;
    }
  }

  &__item {
    display: flex;
    flex-direction: column;
    gap: 16px;

    @include for-desktop {
      &:last-child {
        .contacts__link {
          color: rgba(255, 255, 255, 0.80);
          pointer-events: none;
        }
      }
    }

    @include mobile {
      gap: 12px;
    }
  }

  &__phone,
  &__title {
    font-family: 'Fact Semi Expanded';
    font-size: 24px;
    font-weight: 400;
    color: #fff;
    line-height: 110%;

    @include mobile {
      font-size: 20px;
    }
  }

  &__text {
    color: rgba(255, 255, 255, 0.60);
    line-height: 130%;
  }

  &__link {
    color: #FFBD75;
    line-height: 130%;
    transition: color .2s;

    &:hover {
      color: #F49827;
    }
  }

  .section__title {
    margin-bottom: 0;
    padding-bottom: 24px;
    color: #fff;
    line-height: 102%;
    border-bottom: 1px solid rgba(255, 255, 255, .2);

    @include mobile {
      padding-bottom: 18px;
    }
  }

  &__btn {
    margin-top: 6px;
    padding: 7px 16px;
    display: none;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    border-radius: 8px;
    border: 1px solid #fff;

    &::after {
      content: '';
      display: block;
      width: 24px;
      height: 24px;
      background-image: url('../img/svg/arrow-right-yellow.svg');
    }

    @include mobile {
      display: flex;
    }
  }
}
