.intro {

  &__inner {
    display: grid;
    grid-template-columns: 1fr 410px;
    gap: 30px;

    @include for-desktop {
      grid-template-columns: 1fr 360px;
      gap: 20px;
    }

    @include tablet {
      grid-template-columns: 1fr 1fr;
    }

    @include small-tablet {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    background: #fff;
    border-radius: 20px;
    padding: 30px;

    &:first-child {
      padding: 40px;
      background-image: url('../img/intro/1.png');
      background-size: 100%;
      background-repeat: no-repeat;
      background-position: center bottom;

      @include for-desktop {
        padding: 30px;
      }

      @include small-tablet {
        padding: 24px 20px 80px;
        background-size: 130%;
        background-position: left bottom;
      }
    }

    @include small-tablet {
      padding: 24px 20px;
    }
  }

  &__title {
    span {
      color: #B47A4D;
    }
  }

  &__subtitle {
    margin: 16px 0 20px;
    color: #727272;
    line-height: 130%;

    @include small-tablet {
      margin: 12px 0 18px;
      font-size: 15px;
    }
  }

  &__stats {
    display: flex;
    flex-direction: column;
    gap: 8px;

    &-item {
      padding: 16px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;
      border-bottom: 1px solid #DCDCDC;

      span {
        &:first-child {
          color: #444;
        }

        &:last-child {
          color: #B47A4D;
          font-weight: 500;
        }
      }
    }
  }

  &__link {
    margin-top: 106px;
    padding: 10px 16px;
    border-radius: 8px;
    border: 1px solid #222;
    font-weight: 500;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include for-desktop {
      margin-top: 65px;
    }
    @include small-tablet {
      margin-top: 40px;
    }
  }
}
