.cart {
  margin: 20px 0 82px;

  @include for-desktop {
    margin: 29px 0 60px;
  }

  @include mobile {
    margin-top: 20px;
  }

  &__header {
    margin-bottom: 45px;
    display: flex;
    align-items: flex-end;
    gap: 18px;

    @include for-desktop {
      margin: 0 0 40px;
    }

    @include mobile {
      margin: 0 0 24px;
      gap: 16px;

      .cart__clear {
        flex-grow: 1;
        text-align: right;
      }
    }
  }

  &__inner {
    display: grid;
    align-items: start;
    grid-template-columns: 1fr 370px;
    gap: 70px;

    @include big-desktop {
      gap: 20px;
    }

    @include for-desktop {
      grid-template-columns: 1fr 299px;
    }

    @include tablet {
      grid-template-columns: 1fr;
      gap: 24px;
    }
  }

  &__products-count,
  &__clear {
    margin-bottom: 5px;
    line-height: 130%;

    @include tablet {
      margin-bottom: 2px;
    }
  }

  &__products-count {
    font-size: 18px;
    color: #A2A2A2;

    @include for-desktop {
      font-size: 16px;
    }

    @include mobile {
      font-size: 14px;
    }
  }

  &__clear {
    font-weight: 500;
    color: #B47A4D;

    @include for-desktop {
      font-size: 14px;
    }

    @include mobile {
      font-size: 12px;
    }
  }

  &__form {

    table {
      width: 100%;
    }

    tbody {
      display: flex;
      flex-direction: column;
      gap: 20px;

      @include for-desktop {
        gap: 12px;
      }
    }

    tr {
      border-radius: 20px;

      &.sale {
        .cart__controls .cart__price div {
          color: #F49827;
        }

        @include for-desktop {
          .cart__controls {
            gap: 45px;
          }
        }

        @include small-tablet {
          .cart__card {
            gap: 7px;
          }
        }
      }
    }
  }

  &__card {
    display: flex;
    align-items: center;
    gap: 10px;
    position: relative;

    >div:first-child {
      display: flex;
      align-items: center;
      gap: 22px;
    }

    @include for-desktop {
      >div:first-child {
        display: grid;
        grid-template-columns: 126px 1fr;
      }
    }

    @include small-tablet {
      padding: 16px;
      flex-direction: column;
      align-items: stretch;
      gap: 10px;

      >div:first-child {
        grid-template-columns: 95px 1fr;
        align-items: start;
        gap: 12px;
      }
    }
  }

  &__remove {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 16px;

    a {
      display: flex;
      justify-content: center;
    }

    path {
      transition: stroke .1s linear;
    }

    &:hover {
      path {
        stroke: #222;
      }
    }

    @include small-tablet {
      top: 16px;
      transform: translateY(0);
    }
  }

  &__img {
    margin: 6px 0 2px 6px;
    max-width: 160px;
    border-radius: 15px;
    overflow: hidden;

    @include small-tablet {
      margin: 0;
    }
  }

  &__title {
    font-weight: 500;

    @include for-desktop {
      font-size: 14px;
    }

    @include small-tablet {
      padding-right: 36px;
    }

    @include mobile {
      padding-right: 26px;
    }
  }

  &__info {
    margin: 12px 0;
    display: grid;
    grid-template-columns: min-content 1fr;
    gap: 4px 11px;

    &-name {
      font-size: 14px;
      color: #727272;
    }

    &-value {
      font-size: 14px;
      font-weight: 500;
      color: #444;
    }

    @include for-desktop {

      &-name,
      &-value {
        font-size: 12px;
      }
    }

    @include small-tablet {
      margin: 5px 0;
    }

    @include mobile {
      gap: 4px 35px;
    }
  }

  &__price {

    div {
      display: inline-block;
      font-weight: 500;
    }

    span {
      font-size: 14px;
      color: #727272;
      font-weight: 400;
    }

    &-sale {
      font-size: 12px !important;
      color: #A2A2A2 !important;
      text-decoration: line-through;
    }

    @include for-desktop {
      div {
        font-size: 14px;
      }

      span {
        font-size: 12px;
      }
    }
  }

  &__controls {
    padding-right: 58px;
    flex-grow: 1;
    justify-content: flex-end;
    display: flex;
    align-items: center;
    gap: 40px;

    .cart__price {
      display: flex;
      flex-direction: column;
      align-items: flex-end;
    }

    @include big-desktop {
      gap: 18px;
    }

    @include for-desktop {
      padding-right: 66px;
      flex-direction: column;
      gap: 57px;
      justify-content: space-between;
      align-items: flex-end;

      .cart__price {
        div {
          font-size: 16px;
        }
      }
    }

    @include small-tablet {
      flex-direction: row;
      align-items: center;
      padding-right: 0;
    }
  }

  &__count {
    padding: 4px;
    display: flex;
    align-items: center;
    border-radius: 8px;
    background: #F6F6F4;

    &-remove,
    &-add {
      padding: 2px;
      border-radius: 6px;
      background: #FFF;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;

      path {
        transition: stroke .1s linear;
      }

      &:hover {
        path {
          stroke: rgba(180, 122, 77, 1);
        }
      }
    }

    .quantity input[type=number] {
      background: none;
      border: 0;
      font-size: 14px;
      font-family: inherit;
      color: #444;
      font-weight: 500;
      text-align: center;
      max-width: 39px;
      outline: none;

      -moz-appearance: textfield;
      -webkit-appearance: textfield;
      appearance: textfield;
    }

    .quantity input[type="number"]::-webkit-outer-spin-button,
    .quantity input[type="number"]::-webkit-inner-spin-button {
      display: none;
    }
  }

  &__content {
    @include for-desktop {
      padding: 8px 0;
    }

    @include small-tablet {
      padding: 0;
    }
  }

  &__collaterals {
    padding: 24px;
    border-radius: 20px;

    table {
      margin-top: 20px;
      display: block;

      @include for-desktop {
        margin-top: 16px;
      }
    }

    tbody {
      display: block;
    }

    tr {
      padding: 8px 0;
      display: flex;
      align-items: center;
      justify-content: space-between;

      th {
        color: #727272;
        font-weight: 400;
      }

      &.cart-sale {
        td {
          color: #F49827;
        }
      }

      &.shipping,
      &.cart-payment {
        td {
          color: #727272;
        }
      }

      &.order-total {
        border-top: 1px solid #DCDCDC;
        padding-top: 14px;
        margin-top: 8px;

        th {
          font-size: 18px;
          font-weight: 500;
          color: #444;
          line-height: 130%;
        }

        td {
          font-size: 18px;
          font-weight: 600;
          color: #B47A4D;
        }
      }

      @include for-desktop {
        font-size: 14px;

        &.order-total {

          th,
          td {
            font-size: 16px;
          }
        }
      }

      @include mobile {
        padding: 6px 0;

        &.order-total {
          padding-top: 8px;
        }
      }
    }

    .wc-proceed-to-checkout {
      margin-top: 20px;

      a {
        display: block;
        padding: 14px;
        font-weight: 500;
        color: #fff;
        text-align: center;
        border-radius: 8px;
        background: #B47A4D;

        transition: background-color .2s linear;

        &:hover {
          background-color: darken(#B47A4D, 5%);
        }
      }
    }

    @include for-desktop {
      padding: 20px;
    }

    @include mobile {
      .section__title-sm {
        font-size: 24px;
      }
    }
  }

}


.mini-cart {
  padding: 20px 32px 32px;
  position: absolute;
  top: calc(100% - 14px);
  right: 0;
  width: 571px;
  border-radius: 20px;
  box-shadow: -1px -1px 5px rgba(0, 0, 0, .1);

  opacity: 0;
  visibility: hidden;
  transition: opacity .3s ease-in-out, visibility .3s ease-in-out;

  @include for-desktop {
    padding: 16px 20px 20px;
    top: calc(100% + 10px);
  }

  @include tablet {
    right: 32px;
  }

  @include small-tablet {
    width: calc(100vw - 64px);
  }

  @include mobile {
    top: calc(100% + 15px);
    right: 20px;
    left: 20px;
    width: calc(100vw - 40px);
  }

  &__top {
    position: relative;
    max-height: 434px;

    &::before {
      content: '';
      display: block;
      width: 24px;
      height: 11px;
      position: absolute;
      top: -30px;
      right: 33px;
      background-image: url('../img/cart/caret.svg');
      background-size: cover;

      @include for-desktop {
        top: -27px;
        right: 42px;
      }

      @include tablet {
        right: 96px;
      }
      @include mobile {
        right: 84px;
      }
    }
  }

  &__item {
    &+& {
      margin-top: 40px;

      @include for-desktop {
        margin-top: 28px;
      }
      @include mobile {
        margin-top: 12px;
      }
    }
  }

  &__product {
    display: grid;
    grid-template-columns: 274px 1fr;
    gap: 80px;
    align-items: center;
    position: relative;

    @include for-desktop {
      gap: 25px;
      grid-template-columns: 1fr max-content;
    }

    @include mobile {
      padding: 16px;
      grid-template-columns: 1fr;
      gap: 12px;
    }
  }

  &__title {
    margin-bottom: 35px;
    padding-bottom: 17px;
    font-size: 24px;
    font-weight: 500;
    color: #121212;
    border-bottom: 1px solid #DDD;

    @include for-desktop {
      margin-bottom: 32px;
      padding-bottom: 16px;
      font-size: 18px;
    }

    @include mobile {
      margin-bottom: 16px;
    }
  }

  &__total {
    margin-top: 30px;
    margin-bottom: 24px;
    padding: 24px;
    display: flex;
    justify-content: space-between;
    border-radius: 15px;
    background: #F6F6F4;

    @include for-desktop {
      margin-top: 20px;
      margin-bottom: 12px;
      padding: 20px;
      font-size: 14px;
      border-radius: 8px;
    }
  }

  &__txt {
    color: #727272;
  }

  &__summ {
    font-weight: 500;
  }

  &__link {
    display: block;
    text-align: center;
    padding: 14px 16px;
    font-weight: 500;
    color: #fff;
    border-radius: 8px;
    background: #B47A4D;
    transition: background-color .2s linear;

    &:hover {
      background-color: darken(#B47A4D, 5%);
    }
  }
}

.mini-product {

  &__content {
    display: grid;
    grid-template-columns: 72px 1fr;
    gap: 20px;

    @include mobile {
      grid-template-columns: 95px 1fr;
      gap: 12px;
    }
  }

  &__image {
    img {
      border-radius: 7px;
    }
  }

  &__title {
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: 500;
    color: #121212;

    @include for-desktop {
      font-size: 14px;
    }

    @include mobile {
      padding-right: 20px;
    }
  }

  &__info {
    display: grid;
    grid-template-columns: min-content 1fr;
    gap: 4px 11px;
    font-size: 14px;

    div {
      color: #727272;
    }

    span {
      color: #444;
      font-weight: 500;
    }

    @include for-desktop {
      font-size: 12px;
    }

    @include mobile {
      gap: 4px 35px;
    }
  }

  &__count {
    display: flex;
    align-items: flex-end;
    gap: 15px;

    @include mobile {
      justify-content: space-between;
      padding-top: 12px;
      border-top: 1px solid #DCDCDC;
    }
  }

  &__quantity {
    margin-right: 15px;
    font-size: 14px;
    line-height: 1.3;
    color: #727272;

    @include for-desktop {
      font-size: 12px;
    }
  }

  &__price {

    div {
      font-weight: 500;
    }

    span {
      display: block;
      margin-bottom: -2px;
      font-size: 12px;
      color: #A2A2A2;
      text-decoration: line-through;
    }

    @include for-desktop {
      div {
        font-size: 14px;
      }
    }
    @include mobile {
      display: flex;
      gap: 6px;

      span {
        margin-bottom: 0;
      }

      div {
        font-size: 16px;
      }
    }
  }

  &.sale {
    .mini-product__price div {
      color: #F49827;
    }
  }

  &__delete {
    display: flex;

    path {
      transition: stroke .2s linear;
    }

    &:hover {
      path {
        stroke: #222;
      }
    }

    @include mobile {
      position: absolute;
      top: 16px;
      right: 6px;

      svg {
        width: 20px;
        height: 20px;
      }
    }
  }
}


.simplebar-vertical.simplebar-track {
  right: -15px;
}
