.filter {
  margin-top: 120px;

  ul {
    padding: 0;
    list-style: none;
    display: grid;
    align-items: center;
    gap: 14px;
    grid-template-columns: repeat(11, 1fr);

    input {
      display: none;
    }

    label,
    button {
      display: block;
      width: 100%;
      text-align: center;
      padding: 10px 9px;
      border: 0;
      font-size: 18px;
      font-weight: 500;
      background: #fff;
      border-radius: 8px;
      cursor: pointer;
      line-height: normal;
      border: 2px solid #fff;
      transition: border-color .1s linear;

      &:hover {
        border-color: #222;
      }
    }

    li.checked {

      label,
      button {
        background: #222;
        color: #fff;
        border: 0;

        &:hover {
          border: 0;
        }
      }
    }

    @include for-desktop {
      gap: 12px;
    }

    @include tablet {
      grid-template-columns: repeat(22, 70px);
      grid-auto-flow: column;
      width: 100%;
      overflow: auto;

      label,
      button {
        font-size: 16px;
      }
    }
  }

  @include for-desktop {
    margin-top: 100px;
  }

  @include small-tablet {
    margin-top: 80px;
  }
}
