.about {

  &-intro {
    margin-top: 40px;

    &__inner {
      margin-top: 95px;
      display: grid;
      grid-template-columns: 1fr 670px;
      gap: 72px;
    }

    &__item {

      &:first-child {
        padding-left: 109px;
      }

      p {
        font-size: 18px;
        line-height: 120%;

        +p {
          margin-top: 10px;
        }
      }
    }

    @include for-desktop {
      margin-top: 23px;

      &__inner {
        margin-top: 90px;
        grid-template-columns: 1fr 600px;
        gap: 42px;
      }

      &__item {

        &:first-child {
          padding-left: 0;
        }

        p {
          font-family: 'Inter';
          font-size: 16px;
        }
      }
    }

    @include tablet {
      &__inner {
        margin-top: 40px;
        grid-template-columns: 1fr 1fr;
        gap: 20px;
      }
    }
    @include small-tablet {
      &__inner {
        grid-template-columns: 1fr;
      }
    }

    @include mobile {
      margin-top: 20px;
    }
  }

  &__video {
    position: relative;
    border-radius: 20px;
    overflow: hidden;

    &-play {
      width: 48px;
      height: 48px;
      background-image: url('../img/about/play.svg');
      background-size: contain;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%);
      transition: transform .2s;
      z-index: 2;
      cursor: pointer;

      &:hover {
        transform: translate(-50%, -50%) scale(1.1);
      }
    }

    video {
      width: 100%;
    }

    @include small-tablet {
      border-radius: 10px;
    }
  }

  &-logistics {
    margin: 125px 0 80px;

    .section__title {
      margin-bottom: 0;
    }

    &__header {
      margin-bottom: 40px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 12px;
    }

    &__text {
      padding-right: 30px;
      font-family: 'Inter';
      font-size: 18px;
    }

    &__map {
      position: relative;
      border-radius: 20px;
      overflow: hidden;

      > picture img {
        height: 100%;
        width: 100%;
      }
    }

    &__info {
      padding: 12px;
      display: flex;
      align-items: center;
      gap: 16px;
      position: absolute;
      left: 23px;
      bottom: 18px;
      border-radius: 20px;
      width: 475px;
      background: rgba(246, 246, 244, 0.80);

      div {
        font-size: 18px;
        font-weight: 500;
        line-height: 120%;
        color: #444;
      }
    }

    @include for-desktop {
      margin: 188px 0 80px;

      &__map {
        border-radius: 14px;
        height: 343px;
      }

      &__info {
        gap: 12px;
        left: 14px;
        bottom: 14px;
        width: auto;

        div {
          font-size: 16px;
        }
      }
    }

    @include small-tablet {
      margin: 80px 0;

      &__header {
        margin-bottom: 20px;
        flex-direction: column;
        align-items: flex-start;
      }

      &__map {
        height: 420px;
        border-radius: 12px;
      }

      &__info {
        left: 20px;
        right: 20px;
        bottom: 12px;

        div {
          font-size: 12px;
        }
      }
    }
  }

  &-photos {

    &__inner {
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      gap: 20px 30px;
    }

    &__item {
      border-radius: 20px;
      overflow: hidden;

      img {
        width: 100%;
        height: 100%;
      }

      &:nth-child(3) {
        grid-column: span 2;
        grid-row: span 2;
      }

      &:nth-child(4) {
        grid-column: span 2;
      }
      &:nth-child(5) {
        grid-column: span 2;

        @include small-tablet {
          grid-row: 2 / 3;
        }
      }
    }

    @include for-desktop {
      &__inner {
        grid-template-columns: repeat(4, 1fr);
        gap: 30px;
      }
    }

    @include small-tablet {
      &__inner {
        grid-template-columns: repeat(2, 1fr);
        gap: 20px;
      }
      &__item {
        border-radius: 12px;
      }
    }
  }

  &-documents {
    margin: 120px 0 82px;

    &__item {
      padding: 16px 0 20px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      gap: 20px;
      border-bottom: 1px solid #DCDCDC;
      transition: border-color .2s linear;

      &+& {
        margin-top: 12px;
      }

      span {
        flex-grow: 1;
        justify-self: flex-start;
        font-size: 21px;
        font-weight: 500;
        color: #444;
        line-height: 120%;
      }

      path {
        transition: stroke .2s linear;
      }

      &:hover {
        border-color: #F49827;

        path {
          stroke: #F49827;
        }
      }
    }

    @include for-desktop {
      margin-top: 188px;
      margin-bottom: 60px;
    }

    @include tablet {
      margin-top: 80px;
      margin-bottom: 80px;
    }

    @include small-tablet {
      .section__title {
        margin-bottom: 12px;
      }
    }

    @include mobile {

      &__item {
        gap: 12px;

        &+& {
          margin-top: 8px;
        }

        img {
          width: 36px;
          height: 36px;
        }

        span {
          font-size: 14px;
        }
      }
    }
  }
}
